    }
  }

  .id-logo {
    position: absolute;
    left: 10px;
    top: 15px;
    z-index: 117;

    img {
      width: 55px;
      height: 55px;
    }
  }

  .id-side {
    position: absolute;
    left: 0;
    top: 0;
    width: 20%;
    height: 100%;
    z-index: 107;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .id-back {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
    }
  }
}
